import * as React from 'react';
import * as ReactDOM from 'react-dom';
import * as ReactCSSTransitionGroup from 'react-addons-css-transition-group';
import {Loader} from './sharedcomponents/loader';
import {Popover} from './sharedcomponents/popover/popover';
import {IAppUser} from './services/userservice';
import {LocalisationManager} from './services/i18nservice';
import {injectcontext} from './utils/decorators';
import { Link } from 'react-router';

import './app.header.less';

export interface AppHeaderProps {
    user: IAppUser;
}

@injectcontext({ i18n: React.PropTypes.object.isRequired })
export class AppHeader extends React.Component<AppHeaderProps, any> {
    constructor(props) {
        super(props);
        this.state = {
            showUserPanel: false,
            showUserParent: null,
        }
    }

    showUserPanel(arg) {
        this.setState({
            showUserPanel: true,
            showUserParent: arg.currentTarget
        });
    }

    hideUserPanel() {
        this.setState({
            showUserPanel: false,
            showUserParent: null
        });
    }

    render() {
        var i18n = this.context["i18n"] as LocalisationManager;
        return <header className="app-header">
            <AppSearchBox />
            <div className="right">
                <AppUserEvent user={ this.props.user } onClick={ this.showUserPanel.bind(this) } />
                <div className="spacer"></div>
                <AppUserProfile user={ this.props.user } onClick={ this.showUserPanel.bind(this) } />
            </div>
            <Popover className="userpanel-popover" show={this.state.showUserPanel} parent={this.state.showUserParent} onhide={ this.hideUserPanel.bind(this) }>
                <AppUserPanel user={this.props.user} i18n={i18n} onhide={ this.hideUserPanel.bind(this) } />
            </Popover>
        </header>
    }
}

export class AppSearchBox extends React.Component<any, any> {
    render() {
        return <div className="left">
            <i className="icon dripicons-search" />
            <div className="spacer"></div>
            <input type="search" id="txtsearch" placeholder="search..." />
        </div>
    }
}

export class AppUserEvent extends React.Component<any, any> {
    render() {
        var user = this.props.user as IAppUser;

        if (!user || !user.event) {
            return <div className="user-event">
            </div>
        }
        return <div className="headeritem user-event clickable" onClick={this.props.onClick}>
            <img className="icon" src="assets/local.png" style={{ maxHeight: 26 }} /> <span className="eventname label">{ user.event.name }</span> <i className="chevron dripicons-chevron-down"></i>
        </div>
    }
}

export class AppUserProfile extends React.Component<any, any> {
    render() {
        var user = this.props.user as IAppUser;
        if (!user) {
            return <div className="user-profile">
            </div>
        }
        return <div className="headeritem user-profile clickable" onClick={this.props.onClick}>
            <i className="dripicons-user icon"></i> <span className="username label">{ user.fullname }</span> <i className="chevron dripicons-chevron-down"></i>
        </div>
    }
}

export class AppUserPanel extends React.Component<any, any> {
    setLanguage(arg, lng){
         this.props.i18n.setLanguage(lng);
         this.props.onhide();
    }

    render() {
        var user = this.props.user as IAppUser;
        var i18n = this.props.i18n as LocalisationManager;
        var languages = i18n.supported.map((lng) => {
            return <div key={lng} className={"lng clickable " + (lng == i18n.currentLanguageCode ? " selected" : "")} onClick={ (arg) => this.setLanguage(arg, lng) } >{lng}</div>
        })
        return <div className="userpanel">
            <div className="icon">
                <i className="dripicons-user"></i>
            </div>
            <h4>{ this.props.user.fullname }</h4>
            <div className="languages">{ languages }</div>
        </div>
    }
}


