import * as Promise from 'bluebird';

export interface IAppUser {
    login : string;
    fullname : string;
    permissions : string[];
    event : {
        name : string;
        id: string;
    }
}

export var currentUser : IAppUser = null;

export function isLoggedIn(){
    return currentUser != null;
}

export function signin(login, password){
    return Promise.try(() => {
        currentUser = {
            login : login,
            fullname : "John Doe",
            permissions: [],
            event: {
                name : 'my event',
                id : 'abcd'
            }
        }
        return { authenticated : true, message: null };
    });
}

export function signout(login, password){
    return Promise.try(() => {
        currentUser = null;
    });
}


