import * as $ from 'jquery';
import * as React from 'react';
import * as ReactDOM from 'react-dom';
import * as ReactCSSTransitionGroup from 'react-addons-css-transition-group';
import * as moment from 'moment';
import { Router, Route, IndexRoute, Link, browserHistory, hashHistory, useRouterHistory } from 'react-router';
import {AppShell} from './app.shell';
import {HomePage} from './routes/home/homepage';
import {SpeakersPage} from './routes/speakers/speakerspage';
import {SessionsPage} from './routes/sessions/sessionspage';
import {LoginPage} from './routes/login/loginpage';
import {DataGridPage} from './routes/datagridtests/datagridtests';
import {isLoggedIn} from './services/userservice';

export class AppRoutes extends React.Component<any, any> {
    shouldComponentUpdate(){
        return false;
    }
    
    mustBeLogged(nextState, replace){
        if (!isLoggedIn()) {
            replace({
                pathname: '/login',
                state: { nextPathname: nextState.location.pathname }
            })
        }
    }

    render(){ 
        return <Router history={hashHistory}>
            
            <Route path="/" component={AppShell}>
                <IndexRoute onEnter={ this.mustBeLogged.bind(this) } component={HomePage}/>
                <Route path="login" component={LoginPage} />
                <Route path="sessions"  onEnter={ this.mustBeLogged.bind(this) } component={SessionsPage} />
                <Route path="speakers"  onEnter={ this.mustBeLogged.bind(this) } component={SpeakersPage} />
                <Route path="grid"  onEnter={ this.mustBeLogged.bind(this) } component={DataGridPage} />
            </Route>
        </Router>
    }
}


