import * as React from 'react';
import * as ReactDOM from 'react-dom';
import {FieldBlocProps} from './fieldbloc';

export class TextField extends React.Component<FieldBlocProps, any> {
    render(){
        return <div>
            <label className="forcheckbox" htmlFor={ this.props.schema.key }>{ this.props.schema.labels[this.props.currentLanguage] }</label> 
            <input id={ this.props.schema.key } name={ this.props.schema.key } type="text" />
        </div>
    }
}


