import * as React from 'react';
import * as ReactDOM from 'react-dom';
import * as ReactCSSTransitionGroup from 'react-addons-css-transition-group';
import {Loader} from '../../sharedcomponents/loader';
import {AppPage} from '../../sharedcomponents/page';
import {Card} from '../../sharedcomponents/card';
import {AppLabel} from '../../services/i18nservice';
import {signin} from '../../services/userservice';
import { Router, withRouter } from 'react-router';
import {injectcontext} from '../../utils/decorators';

import './loginpage.less';

export interface LoginPageProps{
    location : { 
        state : {
            nextPathname:string;
        }
    },
    router : any
}

export interface LoginPageState{
    login?:string;
    password?:string;
    message?: string;
}

@injectcontext({ i18n: React.PropTypes.object.isRequired, router: React.PropTypes.object.isRequired })
export class LoginPage extends React.Component<LoginPageProps, LoginPageState> {
    i18nDetach : any;

    constructor(props:LoginPageProps){
        super(props);
        this.state={
            login:"",
            password: "",
            message: null
        }
    }

    componentDidMount(){
        var i18n = this.context["i18n"];
        this.i18nDetach = i18n.attach(()=>{
            this.setState({} as any);
        });
    }

    componentWillUnmount(){
        if (this.i18nDetach){
            this.i18nDetach();
        }
        this.i18nDetach = null;
    }

    trylogin(){
        signin(this.state.login,this.state.password).then((res) => {
            if (res.authenticated){
                const router = this.context["router"] as any;
                const { location } = this.props;
                if (location.state && location.state.nextPathname) {
                    router.replace(location.state.nextPathname)
                } else {
                    router.replace('/')
                }
            }else{
                this.setState({ message : res.message });
            }
        });
    }

    render(){
        var message;
        if (this.state.message){
            message = <p className="message">{ this.state.message}</p>
        }
        return <AppPage className="loginpage" defaultPadding={false} withHeader={false}>
            <Card className="loginform" defaultPadding={false}>
                <header>
                    <img src="assets/logo-lg.png" />                    
                </header>
                <section>
                    <AppLabel component="h3" i18n="loginpage.title" />
                    <form action="#">
                        <p>
                            <label htmlFor="login"><AppLabel i18n="loginpage.login" /></label>
                            <input type="text" id="login" value={this.state.login} onChange={ (arg:any) => this.setState({ login : arg.currentTarget.value}) } />
                        </p>
                        <p>
                            <label htmlFor="password"><AppLabel i18n="loginpage.password" /></label>
                            <input type="password" id="password" value={this.state.password} onChange={ (arg:any) => this.setState({ password : arg.currentTarget.value}) }/>
                        </p>
                        {message}
                    </form>
                </section>
                <footer>
                    <button onClick={ this.trylogin.bind(this) }><AppLabel i18n="loginpage.loginbtn" /></button>
                </footer>
            </Card>
        </AppPage>
    }
}



