import * as React from 'react';
import * as ReactDOM from 'react-dom';
import * as _ from 'lodash';

import {FieldNotSupported} from './notsupported';
import {SelectField} from './selectfield';
import {TextField} from './textfield';
import {BoolField} from './boolfield';

export class FieldBlocProps{
    schema : IEntityFieldSchema;
    currentLanguage : string;
    entity: any;
    onChange: (field:IEntityFieldSchema) => void;
}

var fieldTypes = {
    "selectlist" : SelectField,
    "text" : TextField,
    "bool" : BoolField
}

export class FieldBloc extends React.Component<FieldBlocProps, any> {
    fieldChanged(field:IEntityFieldSchema){
        if (this.props.onChange)
            this.props.onChange(field);
    }

    render(){
        var elt = fieldTypes[this.props.schema.type];
        if (!elt)
            elt = FieldNotSupported;
        var ctrl = React.createElement(elt, _.assign({ onChange:this.fieldChanged.bind(this)}, this.props));
        return <div className="entityfield">
            {ctrl}
        </div>
    }
}


