import * as Promise from 'bluebird';
import * as $ from 'jquery';
import * as React from 'react';
import * as ReactDOM from 'react-dom';
import {injectcontext} from '../utils/decorators';

export class LocalisationManager {
    translations : any;
    isReady : boolean;
    ready : any;
    currentLanguageCode : string;
    currentLanguage : any;
    attached : any[];
    supported : string[];
    defaultlng : string;

    constructor(supported: string[], defaultlng : string){
        var userLang = (navigator.language || navigator.userLanguage).split('-')[0];

        // console.log("language " + userLang);
        this.ready = Promise.resolve({ message : "not loaded" });
        this.translations = {};
        this.supported = supported;
        if (this.supported.indexOf(userLang) >= 0){
            this.currentLanguageCode = userLang;
        }else {
            this.currentLanguageCode = defaultlng;
        }
        this.attached = [];
    }
    
    attach(callback){
        this.attached.push(callback);
        return () => {
            var idx = this.attached.indexOf(callback);
            if (idx >= 0){
                this.attached.splice(idx, 1);
            }
        }
    }

    broadcast(){
        this.attached.forEach((callback) =>{
            callback(this.currentLanguage);
        })
    }

    load() : Promise<any>{
        this.ready = new Promise((resolve, reject) => {
            $.ajax({
                url: 'trads.json'
            }).then((res) => {
                this.isReady = true;
                if (res){
                    this.translations = res;
                    if (this.currentLanguageCode){
                        this.currentLanguage = this.translations[this.currentLanguageCode];
                    }
                }
                resolve();
                this.broadcast();
            }, reject);
        });

        return this.ready;        
    }

    setLanguage(code : string): Promise<any> {
        return this.ready.then(() => {
            if (this.translations && this.translations[code]){
                this.currentLanguageCode = code;
                this.currentLanguage = this.translations[this.currentLanguageCode];
                this.broadcast();
            }        
        });
    }

    translate(key : string){
        if (this.currentLanguage)
            return this.currentLanguage[key] || key;

        return key;
    }
} 

export interface LocalizedProps{
    supported : string[];
    default : string;
    children? : any;
}

export class Localized extends React.Component<LocalizedProps, any> {
    static childContextTypes : React.ValidationMap<any> = {
        i18n: React.PropTypes.object
    };

    constructor(props:LocalizedProps){
        super(props);
        this.state= {
            i18n : new LocalisationManager(props.supported, props.default)
        }
        this.state.i18n.load();
        this.state.i18n.attach(() => this.setState({}));
    }

    getChildContext(): any {
        return {
            i18n:  this.state.i18n
        };
    }

    render(){
        return React.cloneElement(this.props.children)    
    }
}

export interface AppLabelProps {
    i18n : string;
    className? : string;
    component?: string;
}

@injectcontext({ i18n: React.PropTypes.object.isRequired})
export class AppLabel extends React.Component<AppLabelProps, any> {
    render(){
        var i18n = this.context["i18n"] as LocalisationManager;
        if (i18n.isReady){
            return React.createElement(this.props.component || 'span', {className: this.props.className}, i18n.translate(this.props.i18n));
        }
        // if (i18n.ready)
        //     i18n.ready.then(() => this.setState({}));
        
        return React.createElement(this.props.component || 'span', {className: this.props.className});
    }
}


