import * as React from 'react';
import * as ReactDOM from 'react-dom';
import {FieldBlocProps} from './fieldbloc';

export class BoolField extends React.Component<FieldBlocProps, any> {
    valuechanged(arg){
        this.props.entity[this.props.schema.key] = (arg.currentTarget as HTMLInputElement).checked ;
        this.setState({});
        this.props.onChange(this.props.schema);
    }

    render(){
        return <div>
            <input id={ this.props.schema.key } 
                name={ this.props.schema.key }
                value={this.props.entity[this.props.schema.key] || false} 
                onChange={ this.valuechanged.bind(this) }  
                type="checkbox" />
            <label className="forcheckbox" 
                
                htmlFor={ this.props.schema.key }>{ this.props.schema.labels[this.props.currentLanguage] }</label>            
        </div>
    }
}


