import * as React from 'react';
import * as ReactDOM from 'react-dom';
import * as ReactCSSTransitionGroup from 'react-addons-css-transition-group';
import { Link } from 'react-router';
import {AppMenuState} from './app.shell';
import {IAppUser} from './services/userservice';
import {AppLabel} from './services/i18nservice';
import './app.menu.less';


export interface BurgerMenuProps {
    menustate: AppMenuState;
    onClick: (arg) => void;
}

export class BurgerMenu extends React.Component<BurgerMenuProps, any> {
    menuClicked(arg) {
        this.props.onClick(arg);
    }
    render() {
        if (!this.props.menustate.collapsed && !this.props.menustate.allowFixed) {
            document.body.classList.add("burger-on");
        } else {
            document.body.classList.remove("burger-on");
        }
        return <div className={ 'burger-menu' + (!this.props.menustate.collapsed && !this.props.menustate.allowFixed ? ' expanded' : '') } onClick={ this.menuClicked.bind(this) }>
            <div className="burger">
                <div className="split1"></div>
                <div className="split2"></div>
                <div className="split3"></div>
            </div>
        </div>
    }
}

export interface AppMenuProps {
    menustate: AppMenuState;
    visualstate: string[];
    user: IAppUser;
    onShow: (show: boolean) => void;
}

export class AppMenu extends React.Component<AppMenuProps, any> {
    render() {
        return <div className="app-menu">
            <div className="app-menu-content">
                <div className="menu-overlay" onClick={ () => this.props.onShow(false) }></div>
                <div className="menu-bar">
                    <div className="applogo">
                        <Link to="/"><img src="assets/logo-lg.png" /></Link>
                    </div>
                    <div className="menu-bar-content">
                        <AppMenuItems items={menuitems} user={this.props.user} onShow={ this.props.onShow }/>
                    </div>
                </div>
            </div>
        </div>
    }
}

interface MenuItem {
    label: string;
    icon?: string;
    path?: string;
    items?: MenuItem[];    
}

var menuitems = [
    {
        path: '/',
        label: 'menu.home',
        icon: 'assets/dashboard.png'
    },
    {
        label: 'menu.attendees',
        icon: 'assets/user.png',
        items: [
            {
                label: 'menu.attendees.manage',
                path: '/grid',
            },
            {
                label: 'menu.attendees.statistics',
                path: '/sessions',
            },
            {
                label: 'menu.attendees.mailing',
                path: '/speakers',
            }
        ]
    },
    {
        label: 'menu.sessions',
        icon: 'assets/menu-sessions.png',
        items: [
            {
                label: 'menu.sessions.managesessions',
                path: '/sessions',
            },
            {
                label: 'menu.sessions.managespeakers',
                path: '/speakers',
            },
            {
                label: 'menu.sessions.manageexhibitings',
                path: '/sessions',
            }
        ]
    },
    {
        label: 'menu.settings',
        icon: 'assets/settings.png',
        items: [
            {
                label: 'menu.settings.permissions',
                path: '/speakers',
            }
        ]
    }
] as MenuItem[];

export class AppMenuItems extends React.Component<any, any> {
    render() {
        return <div className="menu-items">
            {this.props.items.map((m, idx) => {
                return <AppMenuItem key={idx + '|' + m.path} item={m} onShow={ this.props.onShow } />
            }) }
        </div>
    }
}

export class AppMenuItem extends React.Component<any, any> {
    constructor(props){
        super(props);
        this.state = {
            expanded : false
        }
    }

    toggleState(){
        this.setState({ expanded : !this.state.expanded });
    }

    hideMenu(){
        this.props.onShow(false);
    }

    render() {
        var item = this.props.item as MenuItem;
        var icon;
        if (item.icon){
            icon = <img src={item.icon } className="icon"/>;
        }else{
            icon = <i className="dripicons-chevron-right"></i>;
        }

        if (item.items) {
            var items;
            if (this.state.expanded){
                items = item.items.map((m, idx) => {
                    return <AppMenuItem key={idx + '|' + m.path} item={m} onShow={this.props.onShow} />
                });
            }

            return <div className="menu-item-wrapper">
                <div className={"menu-item clickable" + (this.state.expanded ? " expanded": "") } onClick={ () => this.toggleState()}>
                    {icon}
                    <AppLabel className="label" i18n={item.label} />
                    <i className="chevron dripicons-chevron-right"></i>
                </div>       
                <ReactCSSTransitionGroup component="div" className="menu-item-items" transitionName="fadetransition" transitionEnterTimeout={200} transitionLeave={false}>                    
                    {items}           
                </ReactCSSTransitionGroup>
            </div>
        } else {
            return <div className="menu-item clickable">
                {icon}
                <Link to={ item.path} onClick={ this.hideMenu.bind(this) }>
                    <AppLabel className="label" i18n={item.label} />
                </Link>
            </div>
        }
    }
}


