import * as _ from 'lodash';
import * as React from 'react';
import * as ReactDOM from 'react-dom';
import * as ReactDataGrid from '../../../lib/datagrid/react-data-grid-with-addons';
import { Router, Route, Link, browserHistory } from 'react-router';
import {AppPage} from '../../sharedcomponents/page';

import '../../../lib/datagrid/react-data-grid.css';
import './datagridtests.less';

function createRows(nb){
    var _rows = [];
    for (var i = 1; i < nb; i++) {
        _rows.push({
            id: i,
            titles : "Dr.",
            title: 'Title ' + i,
            count: i * 1000
        });
    }
    return _rows;
}
var Grid = (ReactDataGrid as any);
var DropDownEditor = ReactDataGrid.Editors.DropDownEditor;
var titles = ['Dr.', 'Mr.', 'Mrs.', 'Miss', 'Ms.'];

var columns : ReactDataGridColumn[] = [
    {
        key: 'id',
        name: 'ID',
        sortable : true,
        width : 80
    },
    {
        key: 'titles',
        name: 'C.',
        resizable : true,
        sortable : true,
        // editable:true,
        // editor : <DropDownEditor options={titles}/>
    },
    {
        key: 'title',
        name: 'Title',
        resizable : true,
        sortable : true,
        // editable:true,
    },
    {
        key: 'count',
        resizable : true,
        sortable : true,
        name: 'Count'
    }
]


export class DataGridPage extends React.Component<any, any> {
    constructor(props){
        super(props);
        
        this.state={
            rows: [],
            columns : []
        }
    }

    componentDidMount(){
        setTimeout(() => {
            this.setState({rows : createRows(500), columns : columns});
        }, 350);
    }

    rowGetter(i){
        return this.state.rows[i];
    }

    handleRowUpdated(e:ReactDataGridRowUpdate){
    //merge updated row with current row and rerender by setting state
        var rows = this.state.rows;
        rows[e.rowIdx] = _.assign(rows[e.rowIdx], e.updated);
        this.setState({rows:rows});
    }

    stringSort(column, direction){
        var comparer = (a,b) => a[column].localeCompare(b[column]);
        if (direction == 'DESC'){
            comparer = (a,b) => a[column].localeCompare(b[column])*-1;
        }
        return comparer;
    }

    nbSort(column, direction){
        var comparer = (a,b) => a[column] - b[column];
        if (direction == 'DESC'){
            comparer = (a,b) => b[column] - a[column];
        }
        return comparer;
    }

    getSort(column, direction){
        if (column == 'title' || column == 'titles'){
            return this.stringSort(column,direction);
        } else if (column == 'id' || column == 'count'){
            return this.nbSort(column,direction);
        }
    }

    handleSort(column, direction){
        var comparer = this.getSort(column, direction);
        this.setState({ rows : this.state.rows.sort(comparer) });
    }

    render(){
        return <AppPage className="datagridpage">
            <header>
                <h1>Tests de datagrid</h1>
                <div className="actions">                    
                </div>
            </header>
            <section className="defaultpadding">
                <div className="gridcontainer">
                    <Grid
                        columns={this.state.columns}
                        onGridSort={this.handleSort.bind(this)}
                        rowGetter={this.rowGetter.bind(this)}
                        rowsCount={this.state.rows.length}
                        minHeight={400} 
                        enableCellSelect={true}
                        onRowUpdated={this.handleRowUpdated.bind(this)} />
                </div>
            </section>
        </AppPage>;
    }
}



