import * as _ from 'lodash';
import * as React from 'react';
import {PropTypes} from 'react';
import * as ReactDOM from 'react-dom';
import {Popover} from './popover';
import './modal.less';

export interface ModalProps {
    show: boolean;
    className? : string;
    onhide: () => void;
    addClassNameToBody? : string;
    transitionName? : string
    transitionAppearTimeout?:number;
    transitionEnterTimeout?:number;
    transitionLeaveTimeout?:number;
}

export class Modal extends React.Component<ModalProps, any> {
    render(){
        return <Popover 
                show={this.props.show} 
                onhide={this.props.onhide} 
                className={ "popover-as-modal " + (this.props.className || '')}
                addClassNameToBody={ this.props.addClassNameToBody || "modalpopover-on" } 
                parent={null} 
                useOverlay={ true }
                transitionName={ this.props.transitionName || 'modalpopovertransition'}
                transitionAppearTimeout={this.props.transitionAppearTimeout}
                transitionEnterTimeout={this.props.transitionEnterTimeout}
                transitionLeaveTimeout={this.props.transitionLeaveTimeout}>
            {this.props.children}
        </Popover>
    }
}

export class SideModal extends React.Component<ModalProps, any> {
    render(){
        return <Popover 
                show={this.props.show} 
                onhide={this.props.onhide} 
                className={ "popover-as-sidemodal " + (this.props.className || '')}
                addClassNameToBody={ this.props.addClassNameToBody || "sidemodalpopover-on" } 
                parent={null} 
                useOverlay={ true }
                transitionName={ this.props.transitionName || 'sidemodalpopovertransition'}
                transitionAppearTimeout={this.props.transitionAppearTimeout}
                transitionEnterTimeout={this.props.transitionEnterTimeout}
                transitionLeaveTimeout={this.props.transitionLeaveTimeout}>
            {this.props.children}
        </Popover>
    }
}


