import * as React from 'react';
import * as ReactDOM from 'react-dom';
import {FieldBloc} from './fieldbloc';

import './entityform.less';

export class EntityFormProps{
    schema : IEntitySchema;
    currentLanguage : string;
    entity: any;
}

export class EntityForm extends React.Component<EntityFormProps, any> {
    fieldChanged(fieldSchema){

    }

    render(){
        var fieldChanged = this.fieldChanged.bind(this);
        var fields = this.props.schema.fields.map((field)=>{
            return <FieldBloc key={field.key} schema={field} onChange={fieldChanged} currentLanguage={this.props.currentLanguage} entity={this.props.entity} />
        });

        return <div className="entityform">
            {fields}
        </div>
    }
}


